import React from 'react';
import Carousel from 'react-bootstrap/Carousel';
import { Link } from "react-router-dom";
import twoDogs from '../../assets/twoDogs.png'
import oneDog from '../../assets/oneDog.png'
import wrappedCat from '../../assets/wrappedCat.png'
import * as S from './styled'

const ImagesCarousel = () => {
  return (
    <S.ContainerContent>
      <S.ContainerCarousel>
        <Carousel.Item interval={5000}>
          <S.ImageCarousel src={twoDogs} alt="Dois cachorros" />
          <Carousel.Caption>
            <S.Text>
              AQUI A ADOO  COM
              <br /> MUITA RESPONSABILIDADE!
            </S.Text>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item interval={5000}>
          <S.ImageCarousel src={wrappedCat} alt="Gato coberto com lenol" />
          <Carousel.Caption>
            <S.Text>
              ADOO
              <br />
              NO  BRINCADEIRA!
            </S.Text>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item interval={5000}>
          <S.ImageCarousel src={oneDog} alt="Cachorro" />
          <Carousel.Caption>
            <S.Text>
              ADOTE COM A PET MATCH
              <br />
              SEU MELHOR AMIGO EST AQUI
            </S.Text>
          </Carousel.Caption>
        </Carousel.Item>
      </S.ContainerCarousel>
      <S.ContainerButton>
        <S.ButtonLink color={'#8D73E4'} exact to="/">
          Quero Adotar
        </S.ButtonLink>
        <S.ButtonLink color={'#F76F34'} exact to="/">
          Quero Doar
        </S.ButtonLink>
      </S.ContainerButton>
    </S.ContainerContent>
  );
};

export default ImagesCarousel;
